import React, { useState } from "react";
import { Grid, makeStyles, TextField, Button } from "@material-ui/core";
import { useAuth } from "../FirebaseComponents/AuthContext";
import { useHistory } from "react-router";
import { Link } from "react-router-dom";
const useStyles = makeStyles((theme) => ({
  root: {
    maxHeight: "100vh",
    padding: "40vh 0vh",
  },
  buttonStyle: {
    marginTop: "5vh",
  },
}));
const FirebaseLoginPage = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const classes = useStyles();
  const { login } = useAuth();
  const [loading, setLoading] = useState(false);
  const history = useHistory();
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      setLoading(true);
      await login(email, password);
      history.push("/react-bewine-app/");
    } catch {
      alert("login unsuccessful");
    }
    return setLoading(false);
  };
  return (
    <form className={classes.root} onSubmit={(e) => handleSubmit(e)}>
      <Grid container>
        <Grid container item xs={12} justify="center">
          Login In
        </Grid>
        <Grid container item xs={12} justify="center">
          <TextField
            id="standard-basic"
            label="Email"
            type="email"
            onChange={(e) => {
              setEmail(e.target.value);
            }}
          />
        </Grid>
        <Grid container item xs={12} justify="center">
          <TextField
            id="standard-basic"
            type="password"
            label="Password"
            onChange={(e) => {
              setPassword(e.target.value);
            }}
          />
        </Grid>
        <Grid container item xs={12} justify="center">
          <Button
            variant="contained"
            type="submit"
            className={classes.buttonStyle}
          >
            Log In
          </Button>
        </Grid>
        <Grid container item xs={12} justify="center">
          <Button className={classes.buttonStyle} disable={loading}>
            Don't Have An Account?{" "}
            <Link to="/react-bewine-app/signup"> Sign Up</Link>
          </Button>
        </Grid>
      </Grid>
    </form>
  );
};

export default FirebaseLoginPage;
