import React from "react";
import NavBar from "./NavBar";
import Footer from "./Footer";
import HomePage from "./components/Pages/HomePage";
import AboutUsPage from "./components/Pages/AboutUsPage";
import JoinNow from "./components/HomeComponent/JoinNow";
import Wines from "./components/Pages/Wines";
import ContactPage from "./components/Pages/ContactPage";
import FirebaseDashBoard from "./components/Pages/FirebaseDashBoard";
import FirebaseLoginPage from "./components/Pages/FirebaseLoginPage";
import FirebaseSignUpPage from "./components/Pages/FirebaseSignUpPage";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import AuthProvider from "./components/FirebaseComponents/AuthContext";
import PirvateRoute from "./components/FirebaseComponents/PirvateRoute";
const App = () => {
  return (
    <Router>
      <NavBar></NavBar>

      {/* <Route exact path="/react-bewine-app/">
          <HomePage />
        </Route>
        <Route exact path="/react-bewine-app/about">
          <AboutUsPage />
        </Route>
        <Route exact path="/react-bewine-app/wines">
          <Wines />
        </Route>
        <Route exact path="/react-bewine-app/contact">
          <ContactPage />
        </Route> */}
      <AuthProvider>
        <Switch>
          <PirvateRoute
            exact
            path="/react-bewine-app"
            component={<FirebaseDashBoard />}
          ></PirvateRoute>
          <Route exact path="/react-bewine-app/login">
            <FirebaseLoginPage />
          </Route>
          <Route exact path="/react-bewine-app/signup">
            <FirebaseSignUpPage />
          </Route>
        </Switch>
      </AuthProvider>

      {/* <JoinNow />
      <Footer /> */}
    </Router>
  );
};

export default App;
