import React, { useState } from "react";
import { Grid, makeStyles, TextField, Button } from "@material-ui/core";
import { Link, useHistory } from "react-router-dom";
import { useAuth } from "../FirebaseComponents/AuthContext";
import { auth } from "../../firebase";
const useStyles = makeStyles((theme) => ({
  root: {
    maxHeight: "100vh",
    padding: "20vh 0vh",
  },
  buttonStyle: {
    marginTop: "5vh",
  },
}));
const FirebaseSignUpPage = () => {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const { signup, currentUser } = useAuth();
  const [loading, setLoading] = useState(false);
  const classes = useStyles();
  const history = useHistory();

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (password != confirmPassword) {
      return alert("The password is not match");
    }
    try {
      setLoading(true);
      await signup(email, password);
    } catch {
      alert("Failed to create an account");
    }
    try {
      await auth.currentUser.updateProfile({
        displayName: name,
      });
      history.push("/react-bewine-app/");
    } catch {
      alert("Failed to assign displyName");
    }
    return setLoading(false);
  };
  return (
    <form className={classes.root} onSubmit={(e) => handleSubmit(e)}>
      <Grid container>
        <Grid container item xs={12} justify="center">
          Sign Up
        </Grid>
        <Grid container item xs={12} justify="center">
          <TextField
            id="standard-basic"
            label="Name"
            type="name"
            onChange={(e) => {
              setName(e.target.value);
            }}
          />
        </Grid>
        <Grid container item xs={12} justify="center">
          <TextField
            id="standard-basic"
            label="Email"
            type="email"
            onChange={(e) => {
              setEmail(e.target.value);
            }}
          />
        </Grid>
        <Grid container item xs={12} justify="center">
          <TextField
            id="standard-basic"
            type="password"
            label="Password"
            onChange={(e) => {
              setPassword(e.target.value);
            }}
          />
        </Grid>
        <Grid container item xs={12} justify="center">
          <TextField
            id="standard-basic"
            label="Confirm Password"
            type="password"
            onChange={(e) => {
              setConfirmPassword(e.target.value);
            }}
          />
        </Grid>
        <Grid container item xs={12} justify="center">
          <Button
            variant="contained"
            type="submit"
            className={classes.buttonStyle}
            disable={loading}
          >
            Sign Up
          </Button>
        </Grid>
        <Grid container item xs={12} justify="center">
          <Button className={classes.buttonStyle} disable={loading}>
            Already Have An Acoount?{" "}
            <Link to="/react-bewine-app/login"> Log In</Link>
          </Button>
        </Grid>
      </Grid>
    </form>
  );
};

export default FirebaseSignUpPage;
