import React, { useState } from "react";
import {
  Button,
  Grid,
  TextField,
  Paper,
  TableContainer,
  TableHead,
  TableCell,
  Table,
  TableRow,
  TableBody,
  Select,
  MenuItem,
} from "@material-ui/core";
import { useAuth } from "../FirebaseComponents/AuthContext";

import { Link, useHistory } from "react-router-dom";
import { auth } from "../../firebase";
const FirebaseDashBoard = () => {
  const [title, setTitle] = useState("");
  const [category, setCategory] = useState("history");
  const [historyArray, sethistoryArray] = useState([
    { author: "Kaung", category: "history", title: "Vietnam War" },
    { author: "Nyein", category: "history", title: "American Civil War" },
    { author: "Nyein", category: "science", title: "Relativity simplify" },
    {
      author: "Kaung",
      category: "science",
      title: "Is NASA a waste of money??????",
    },
  ]);
  const { currentUser, logout } = useAuth();
  const history = useHistory();
  const handleLogout = async () => {
    try {
      await logout();
      history.push("/react-bewine-app/");
    } catch {
      alert("Log Out unsuccessful");
    }
  };
  const handleSubmit = (e) => {
    e.preventDefault();
    sethistoryArray([
      ...historyArray,
      { author: "Kaung", category: category, title: title },
    ]);
    setCategory("history");
    setTitle("");
  };
  const handleSelect = (e) => {
    setCategory(e.target.value);
  };

  return (
    <Grid container xs={12}>
      <Grid item container xs={12} justify="center">
        <h1>Welcome From The DashBoard</h1>
      </Grid>
      <Grid item container xs={12} justify="center">
        <form onSubmit={handleSubmit}>
          <TextField
            id="standard-basic"
            label="title"
            type="name"
            onChange={(e) => {
              setTitle(e.target.value);
            }}
          />
          <Select
            labelId="demo-simple-select-label"
            id="demo-simple-select"
            value={category}
            onChange={handleSelect}
          >
            <MenuItem value="history">History</MenuItem>
            <MenuItem value="science">Science</MenuItem>
          </Select>
          <Button variant="contained" type="submit">
            Add
          </Button>
        </form>
      </Grid>
      <Grid item container xs={12} justify="center">
        <Grid item container xs={12} justify="center">
          <h2>History</h2>
        </Grid>
        <Grid item container xs={12} justify="center">
          <TableContainer component={Paper}>
            <Table aria-label="simple table">
              <TableHead>
                <TableRow>
                  <TableCell>Author</TableCell>
                  <TableCell align="right">Title</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {historyArray
                  .filter(
                    (historyNotFilter) => historyNotFilter.category == "history"
                  )
                  .map((historyElement) => (
                    <TableRow key={historyElement.name}>
                      <TableCell component="th" scope="row">
                        {historyElement.author}
                      </TableCell>
                      <TableCell align="right">
                        {historyElement.title}
                      </TableCell>
                    </TableRow>
                  ))}
              </TableBody>
            </Table>
          </TableContainer>
        </Grid>
      </Grid>
      <Grid item container xs={12} justify="center">
        <Grid item container xs={12} justify="center">
          <h2>Science</h2>
        </Grid>
        <Grid item container xs={12} justify="center">
          <TableContainer component={Paper}>
            <Table aria-label="simple table">
              <TableHead>
                <TableRow>
                  <TableCell>Author</TableCell>
                  <TableCell align="right">Title</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {historyArray
                  .filter(
                    (historyNotFilter) => historyNotFilter.category == "science"
                  )
                  .map((historyElement) => (
                    <TableRow key={historyElement.name}>
                      <TableCell component="th" scope="row">
                        {historyElement.author}
                      </TableCell>
                      <TableCell align="right">
                        {historyElement.title}
                      </TableCell>
                    </TableRow>
                  ))}
              </TableBody>
            </Table>
          </TableContainer>
        </Grid>
      </Grid>
      <Button variant="contained" onClick={handleLogout}>
        Log out
      </Button>
    </Grid>
  );
};

export default FirebaseDashBoard;
